<template>
  <div class="home">
    <div class="bgcBox">
      <img src="../../../static/images/3e296bfaebb06a42aac886b1adef5cd.jpg" alt />
    </div>
    <div class="classList mt72 bf bx">
      <div class="bigBox">
        <div class="boxLeft">
          <div class="xueke">
            <el-cascader :options="options" v-model="selectedOptions" @change="handleChange"></el-cascader>
          </div>
          <div class="three">
            <subject />
          </div>
          <div class="threeBtn">
            <el-button
              style="background-color:#2ab8b3;color:#fff;border:0"
              @click="Towrongbooklist"
            >章错题重考</el-button>
            <el-button style="background-color:#ff8b00;color:#fff;border:0">错题转正&nbsp;&nbsp;&nbsp;</el-button>
          </div>
        </div>
        <div class="boxRight">
          <div class="tupu">
            <!-- <knowledge /> -->
            <img src="../../../static/images/tupu.png" alt />
          </div>
          <div class="myChoores">
            <span>错题原因:</span>
            <el-checkbox-group v-model="checkList" class="choores">
              <el-checkbox label="知识记忆"></el-checkbox>
              <el-checkbox label="粗心大意"></el-checkbox>
              <el-checkbox label="运算能力"></el-checkbox>
              <el-checkbox label="逻辑混乱"></el-checkbox>
              <el-checkbox label="阅读能力"></el-checkbox>
            </el-checkbox-group>
          </div>
          <div class="myWrong">
            <div class="myWrongList">
              <p class="number">编号:22545</p>
              <img src="../../../static/images/题目.jpg" alt />
              <div class="worngTime">
                <span>来源:陈老师</span>
                <span style="margin-left:30px">答题时间:2019-12-22&nbsp;&nbsp;11:11:11</span>
                <div class="myWrongBtn" v-for="(item, index) in wrongBtn" :key="index">
                  <el-button
                    :class="{ active: num2 == index }"
                    @click="ToWork(index, item)"
                  >{{ item.name }}</el-button>
                </div>
              </div>
              <div class="myAnswer" v-if="analysis">
                <div class="answerBox">
                  <p>
                    <i class="el-icon-caret-right"></i>
                    <span>我的答案：A</span>
                  </p>
                  <p>
                    <i class="el-icon-caret-right"></i>
                    <span style="color:#2ab8b3">正确答案：B</span>
                  </p>
                </div>
                <p class="jiexi">
                  <i class="el-icon-caret-right"></i>
                  <span>解析：</span>
                  <img src="../../../static/images/解析.png" alt />
                </p>
              </div>
              <div class="myAnswers" v-if="Tagging">
                <div class="answerBox">
                  <p style="margin-bottom:10px">
                    <i class="el-icon-caret-right"></i>
                    <span>我的标注：</span>
                  </p>
                  <el-checkbox-group v-model="checkList" class="choores">
                    <el-checkbox label="知识记忆"></el-checkbox>
                    <el-checkbox label="粗心大意"></el-checkbox>
                    <el-checkbox label="运算能力"></el-checkbox>
                    <el-checkbox label="逻辑混乱"></el-checkbox>
                    <el-checkbox label="阅读能力"></el-checkbox>
                  </el-checkbox-group>
                </div>
                <el-button class="myWrongbtn">提交</el-button>
              </div>
              <div class="myAnswers" v-if="redo">
                <div class="answerBox">
                  <p style="margin-bottom:10px">
                    <i class="el-icon-caret-right"></i>
                    <span>类似题目：</span>
                  </p>
                  <img src="../../../static/images/错题重做.png" alt />
                </div>
                <el-button class="myWrongbtn">提交</el-button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import subject from "../../components/grade/subject";
import knowledge from "../../views/homeCommon/resourceTools/sb";
export default {
  components: {
    knowledge,
    subject
  },
  data() {
    return {
      analysis: true, //解析框
      Tagging: false, //错题标注
      redo: false,
      input: "", //标注
      wrongBtn: [
        { name: "错题转正", index: 0 },
        { name: "相似重做", index: 1 },
        { name: "错因标注", index: 2 },
        { name: "错题解析", index: 3 }
      ],
      checkList: ["选中且禁用", "复选框 A"],
      options: [
        {
          value: "选项1",
          label: "语文"
        },
        {
          value: "选项2",
          label: "数学"
        }
      ],
      data: [
        {
          label: "章",
          children: [
            {
              label: "节",
              children: [
                {
                  label: "单元"
                }
              ]
            }
          ]
        }
      ],
      defaultProps: {
        children: "children",
        label: "label"
      },
      List: [
        {
          index: 1,
          labels: "完整图谱"
        },
        {
          index: 2,
          labels: "单元图谱"
        },
        {
          index: 3,
          labels: "作业图谱"
        }
      ],
      list1: [
        {
          index: 1,
          label: "未完成"
        },
        {
          index: 2,
          label: "已完成"
        }
      ],
      num: 0,
      num2: 0,
      selectedOptions: []
    };
  },
  methods: {
    Towrongbooklist() {
      this.$router.push("/studentCommon/wrongbooklist");
    },
    Tochart(index, item) {
      this.num = index;
    },
    ToWork(index, item) {
      this.num2 = index;
      if (index == 1) {
        this.redo = !this.redo;
        this.Tagging = false;
        this.analysis = false;
      }
      if (index == 2) {
        this.Tagging = !this.Tagging;
        this.analysis = false;
        this.redo = false;
      }
      if (index == 3) {
        this.analysis = !this.analysis;
        this.redo = false;
        this.Tagging = false;
      }
      if (index == 0) {
        this.analysis = false;
        this.redo = false;
        this.Tagging = false;
        this.$message({
          type: "success",
          message: "错题转正成功"
        });
      }
    },
    handleChange(value) {
      // console.log(value);
    }
  }
};
</script>
<style lang="less" scoped>
.classList {
  border: 1px solid #eee;

  // padding: 15px 0px;
  .active {
    background: #2ab8b3;
    color: #fff;
  }
  .active2 {
    // background: #2ab8b3;
    // color: #fff;
    background-color: black;
  }

  .bigBox {
    width: 100%;
    height: 100%;
    padding: 10px 0px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    // overflow: hidden;
    .boxLeft {
      width: 17%;
      // border-right: 1px solid #eee;
      padding: 10px;
      box-sizing: border-box;
      border-right: 10px solid #eee;
      .xueke {
        width: 100%;
        padding: 3px;
      }
      .three {
        padding: 10px 0px;
        border-top: 1px solid #eee;
        width: 100%;
        margin-top: 20px;
        box-sizing: border-box;
      }
      .threeBtn {
        button {
          margin-top: 15px;
          margin-left: 40px;
        }
      }
    }
    .boxRight {
      flex: 1;
      padding: 15px;
      .tupu {
        width: 95%;
        height: 500px;
        border: 1px solid #ccc;
        margin: 0 auto;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .myChoores {
        height: 90px;
        border-top: 1px solid #eee;
        margin: 0 25px;
        //  padding-top: 10px;
        line-height: 90px;
        .choores {
          display: inline-block;
          margin-left: 10px;
        }
      }
      .myWrong {
        margin: 0 30px;
        border: 1px solid #2ab8b3;
        .myWrongList {
          .number {
            margin-left: 15px;
            display: block;
            padding: 15px 0;
            color: #f2cca0;
          }
          .worngTime {
            height: 53px;
            background-color: #f8f8f8;
            line-height: 53px;
            padding-left: 15px;
            .myWrongBtn {
              height: 100%;
              float: right;
              background-color: #f8f8f8;
              button {
                height: 99%;
                margin-bottom: 2px;
                border: 0;
                border-right: 1px solid #eee;
              }
            }
          }
          .myAnswer {
            padding: 0px 15px;
            box-sizing: border-box;
            i {
              color: #febb50;
            }
            .answerBox {
              height: 95px;
              border-bottom: 1px solid #eee;
              line-height: 42.5px;
            }
            .jiexi {
              padding: 20px 0;
            }
          }
          .myAnswers {
            padding: 10px 15px;

            i {
              color: #febb50;
            }
            .myWrongbtn {
              background-color: #2ab8b3;
              color: #fff;
              border: 0;
              margin-top: 10px;
              margin-left: 93%;
            }
          }
        }
      }
    }
  }
}
</style>



